.wysiwyg{--mdb-wysiwyg-text-color: var(--mdb-surface-color);--mdb-wysiwyg-bg: var(--mdb-surface-bg);--mdb-wysiwyg-border-color: var(--mdb-border-color);--mdb-wysiwyg-hover: var(--mdb-highlight-bg-color);--mdb-wysiwyg-toolbar-bg: var(--mdb-tertiary-bg);width:100%;border:1px solid var(--mdb-wysiwyg-border-color)}.wysiwyg-textarea{width:0px;height:0px;visibility:hidden;padding:0;margin:0;border:0}.wysiwyg-content{min-height:450px;padding:.5rem;background-color:var(--mdb-wysiwyg-bg);color:var(--mdb-wysiwyg-text-color);font-family:"Roboto",sans-serif;font-size:16px;font-weight:400;text-align:start}.wysiwyg-content:focus{outline:none}.wysiwyg-content .wysiwyg-show-html{white-space:pre-line}.wysiwyg-toolbar{background-color:var(--mdb-wysiwyg-toolbar-bg);padding:.3rem 0 .3rem 0}.wysiwyg-toolbar-group:not(:first-child):not(.ml-auto):before{content:"";position:absolute;height:1.2rem;width:0;border-right:1px solid #bcbcbc;margin-top:.4rem}.wysiwyg-toolbar-group .dropdown-menu{min-width:8rem}.wysiwyg-color{height:2rem;width:2rem;border-radius:0;padding:0}.wysiwyg-toolbar-group.wysiwyg-hide{display:none}.wysiwyg-toolbar-toggler .dropdown-menu{min-width:0}.wysiwyg-toolbar-toggler .dropdown-menu .mx-1{margin-right:0 !important;margin-left:0 !important;border-bottom:1px solid var(--mdb-wysiwyg-border-color);border-right:1px solid var(--mdb-wysiwyg-border-color);border-radius:0}.btn-group>.btn.wysiwyg-btn{background-color:transparent;min-width:32px;min-height:32px;padding-left:.5rem;padding-right:.5rem}.btn-group>.btn.wysiwyg-btn i{color:var(--mdb-wysiwyg-text-color)}.btn-group>.btn.wysiwyg-btn:hover{background-color:var(--mdb-wysiwyg-hover)}